#masthead-content .name {
    font-size: 1.75em;
    display: block;
}

#masthead-content button {
    margin: 0 auto;
    display: block;
}
#masthead {
    position: relative;
    margin: 0;
    border-radius: 0;
}

#masthead .ui.container {
    position: relative;
    min-height: 600px;
    vertical-align: top;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.following.bar {
    position: fixed;
    z-index: 100;
    width: 100%;
    padding: 2em 0em;
}
